.LinhaRegistro {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

.LinhaRegistro .Coluna1{
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-right: 20px;
}
.LinhaRegistro .Coluna2{
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-right: 12px;
}
.LinhaRegistro .Coluna3{
  display: flex;
  flex-direction: column;
  text-align: center;
}
.LinhaRegistro .Coluna4{
  display: flex;
