.BotaoRegistrar{
    color: #FFFFFF;
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    background-color: #4c6ef5;
    border-radius: 15px;
    border: none;
    padding: 2px 15px;
    margin-top: 5px;
    width: 120px;
    height: 25px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .BotaoRegistrar:hover{
    background-color: #293c88;
  }